                   v-if=\"invi?.ceraStart && invi?.ceraEnd\"\n                    class=\"guest-dash-info\"\n                >\n                    <img src=\"/assets/guestDash/calender.svg\" alt=\"\" />\n                    <span\n                        ><b>Ceremonies:</b>\n                        <!-- {{ invi.ceraStart | moment(\"dddd, MMMM D, YYYY\") }} to\n                        {{ invi.ceraEnd | moment(\"dddd, MMMM D, YYYY\") }} -->\n                        {{ invi.startDate | moment(\"dddd, MMMM D, YYYY\") }}\n                    </span>\n                </div>\n                <div v-if=\"invi.venues.length > 0\" class=\"guest-dash-info\">\n                    <img src=\"/assets/guestDash/location.svg\" alt=\"\" />\n                    <span v-for=\"e in invi.venues\" :key=\"e.name\"\n                        >{{ e.name }}, {{ e.location.name }},\n                        {{ e.location.state }}\n                    </span>\n                </div>\n            </div>\n        </a>\n        <!-- <div class=\"guest-dash-card-btns\">\n            <button title=\"Pending\" @click=\"$emit('view', invi)\" type=\"button\">\n                <img src=\"/assets/guestDash/eye.svg\" alt=\"\" />\n            </button>\n            <button title=\"Accept\" @click=\"$emit('accept', invi)\" type=\"button\">\n                <img src=\"/assets/guestDash/check.svg\" alt=\"\" />\n            </button>\n            <button\n                title=\"Decline\"\n                @click=\"$emit('decline', invi)\"\n                type=\"button\"\n            >\n                <img src=\"/assets/guestDash/x.svg\" alt=\"\" />\n            </button>\n        </div> -->\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"invi\", \"user\", \"active\"],\n    computed: {\n        setClass() {\n            if (this.active == \"accepted\") {\n                return \"green\";\n            }\n            if (this.active == \"declined\") {\n                return \"red\";\n            }\n            if (this.active == \"pending\") {\n                return \"yellow\";\n            }\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* card csss start */\n.red {\n    background: #c94b33 !important;\n}\n.yellow {\n    background: #e26b00 !important;\n}\n.green {\n    background: #23871d !important;\n}\n.guest-dash-card {\n    background: #ffffff;\n    border: 7.91317px solid #f3f3f3;\n    border-radius: 6.37724px;\n    padding: 3px;\n    max-width: 213px;\n    width: 100%;\n    margin-right: 10px;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n.guest-dash-card-content {\n    padding: 8px;\n    background: #ffffff;\n    display: flex;\n    text-decoration: none;\n    flex-direction: column;\n}\n.img-cont {\n    position: relative;\n    padding-top: 80%;\n    margin-bottom: 15px;\n}\n.guest-dash-card-content > .img-cont > img {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n\n    position: absolute;\n    top: 0;\n    left: 0;\n}\n.guest-dash-card-head {\n    display: flex;\n    align-items: center;\n}\n.guest-dash-card-head > span:nth-child(1) {\n    background: #461952;\n    border-radius: 6.37724px;\n    padding: 7px 8px;\n    display: inline-block;\n    text-align: center;\n    vertical-align: center;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 13.1462px;\n    line-height: 20px;\n    margin-right: 10px;\n    color: #ffffff;\n}\n.guest-dash-card-head > span:nth-child(1) > span {\n    font-weight: 300;\n    font-size: 9.14521px;\n    line-height: 14px;\n    display: block;\n}\n.guest-dash-card-head > span:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 13.1462px;\n    line-height: 20px;\n    color: #000000;\n}\n.guest-dash-info {\n    display: flex;\n    align-items: center;\n    margin: 5px 0;\n}\n.guest-dash-info > img {\n    margin-right: 10px;\n}\n.guest-dash-info > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 9.81904px;\n    line-height: 15px;\n    color: #99a0af;\n}\n.guest-dash-card-btns {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 5px;\n    border-top: 5.91317px solid #f3f3f3;\n}\n.guest-dash-card-btns > button {\n    border: none;\n    outline: none;\n    background: #ffffff;\n    border-radius: 2.5509px;\n    width: 66px;\n    height: 43px;\n    display: block;\n}\n\n/* card css end */\n</style>\n"],"sourceRoot":""}]);
